//$background: linear-gradient(#E1B339,#EB7200);
$background: linear-gradient(#E1B339, #EB7200);
$backgroundAlmostTransparent: rgba(255,255,255, 0.4);
$search-bar-color: transparent;
$search-bar-background: transparent;
$box-gradient: transparent;
$border-top: 0.5px solid rgba(255, 255, 255, 0.2);


* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

body {
  background: no-repeat fixed $background;
  height: 100vh;
}

.main {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.container {
  width: 95%;
  max-width: 1000px;
  margin: 72px 0 0 0;
  height: 85vh;
}

.placeholder-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  align-items: center;
  justify-items: center;
  gap: 10px;
  height: 100%;
}

.icon {
  transform: rotate(180deg);
}

.top-bar {
  position: fixed;
  width: 100%;
  max-height: 65px;
  background: $search-bar-color;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-box-container {
  width: 90%;
  max-width: 1000px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.search-box__search-bar {
  width: 90%;
  max-width: 1000px;
  height: 5vh;
  background: $search-bar-background;
  border: none;
  text-align: left;
  color: white;
  font-size: 1.5rem;
  margin: 10px 0 10px 0;
  padding: 10px;


  &::placeholder {
    max-width: 1000px;
    height: 5vh;
    text-align: center;
    color: white;
    font-size: 1.5rem;
    margin: 10px 0 10px 0;
    padding: 10px;
  }
}

.unit-button {
  height: 40px;
  width: 40px;
  color: white;
  margin-left: 5px;
  font-size: 1.5rem;
  background: $search-bar-background;
  border: none;
}

.unit-button-active {
  border: 1px solid $backgroundAlmostTransparent;
  border-radius: 15px;

}

.location-box {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  color: white;
  margin-bottom: 10px;
}

.placeholder {
  width: 100%;
  height: 100%;
  border-radius: 12px;
  flex-direction: column;
  justify-content: space-evenly;
  background: rgba(0, 0, 0, 0.02);
}

.location-box__top {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.location-box__bottom {
  border-top: $border-top;
  width: 100%;
  margin-top: 20px;

}

.location-box__desc {
  display: flex;
  justify-content: space-between;
  gap: 10px;
  font-size: 12px;
  margin-top: 10px;
}

.splitter {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.reversed {
  text-align: end;
}

.weather_icon {
  width: 30px;
}

.info-box__name {
  text-transform: capitalize;
}


.forecast-hour {
  overflow-x: scroll;
  padding: 10px 20px 10px 20px;
  margin: 10px 0;
  color: white;
  display: flex;
  border-top: $border-top;

  &::-webkit-scrollbar {
    background: transparent;
    height: 8px;
  }

  &::-webkit-scrollbar-thumb {
    background: rgba(0, 0, 0, 0.1);
    border-radius: 15px;
  }
}

.forecast-day {
  margin: 10px 0;
  color: white;
  display: flex;
  flex-direction: column;
  border-top: $border-top;
}

.single-hour {
  display: flex;
  flex-direction: column;
  padding-right: 10%;

  &:last-of-type {
    padding-right: 0;
  }
}

.single-day {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  justify-items: start;
  margin-top: 10px;
}

.single-day__weather-desc{
  text-transform: capitalize;
  grid-column: span 2;
}

.single-day__temp{
  display: flex;
}

.not-found {
  text-align: center;
  color: white;
  border-top: $border-top;
  font-size: 4vh;
  padding-top: 50px;
}
.a {
  color: black;
}
